import React, {Component} from 'react';
import './galery.css'
import Gbr1 from './imagegallery/gambar1.JPG'
import Gbr2 from './imagegallery/gambar2.JPG'
import Gbr3 from './imagegallery/gambar3.JPG'
import Gbr4 from './imagegallery/gambar4.JPG'
import Gbr5 from './imagegallery/gambar5.JPG'
import Gbr6 from './imagegallery/gambar6.JPG'

class Gallery extends Component{
  
  state={
      gambar:[
          {url:Gbr1},
          {url:Gbr2},
          {url:Gbr3},
          {url:Gbr4},
          {url:Gbr5},
          {url:Gbr6},
      ]
  }



    render(){
        return(

            <div className='container-gallery'>
                <h2 className="galery-h2">Gallery</h2>
              
                
                
                 
              <div className="row justify-content-center ">
              <div className="col-md-8 deep-container-gallery ">
              
              <div className="row ">
                    {this.state.gambar.map((m,i) => 
                         
                             
                              
                              <a href={m.url}  key={i}data-toggle="lightbox" data-gallery="example-gallery" className="col-sm-4">
                               <img src={m.url} className="img-fluid" style={{margin:2}} />
                              </a>
                             
                             
                        
                        )}
                       </div>
                       </div>
                       </div>
                    

                
              
            </div>
        )
    }
}

export default Gallery;