import React from "react";
import "./navbar.css"
import logo from "../images/logo dhoho.png"
import Tokopedia from "../images/tokopedia.png"
function Navbar(){
    return(
        <nav className="navbar navbar-expand-lg navbar-light bg-transparant">
            <a className="navbar-brand" href="#">
                <h3 className="logo"><b>KACANG</b></h3>
                <h3 className="logo2"><i><b>Pedas</b></i></h3>
                <img src={logo} className="d-inline-block align-top" style={{width:60, height:50, marginLeft:9}}/>
            </a>
             
            
             <span className="navbar-text ml-auto span">
             <a href='https://www.tokopedia.com/camilankhasnusan/kacang-pedasa-dhoho' target="_blank">
                 belanja...
                  <img src={Tokopedia} style={{width:50, height:50}} />
                 </a>
                
             </span>
        </nav>
    )
}

export default Navbar;